
"use client";

import * as React from "react";
import type { Tournament } from "@/lib/types";
import { format, isAfter, parseISO } from "date-fns";
import Link from "next/link";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { ExternalLink, Info } from "lucide-react";
import { Badge } from "@/components/ui/badge";
import { cn } from "@/lib/utils";

const TOURNAMENTS_STORAGE_KEY = 'work-session-tournaments-list';

export default function DashboardPage() {
  const [tournaments, setTournaments] = React.useState<Tournament[]>([]);
  const [isMounted, setIsMounted] = React.useState(false);

  React.useEffect(() => {
    if (typeof window !== 'undefined') {
      try {
        const stored = localStorage.getItem(TOURNAMENTS_STORAGE_KEY);
        const parsedTournaments: Tournament[] = stored ? JSON.parse(stored) : [];
        const upcomingTournaments = parsedTournaments
          .filter(t => isAfter(parseISO(t.date), new Date()))
          .sort((a, b) => new Date(a.date).getTime() - new Date(b.date).getTime());
        setTournaments(upcomingTournaments);
      } catch (error) {
        console.error("Failed to parse tournaments from localStorage", error);
        setTournaments([]);
      }
      setIsMounted(true);
    }
  }, []);
  
  if (!isMounted) {
     return <div className="flex items-center justify-center h-96">Loading...</div>;
  }

  return (
    <div className="flex flex-col gap-6">
      <div>
        <h1 className="text-3xl font-bold font-headline">Dashboard</h1>
        <p className="text-muted-foreground">
          An overview of upcoming tournaments.
        </p>
      </div>

      <Card>
        <CardHeader>
          <CardTitle>Upcoming Tournaments</CardTitle>
        </CardHeader>
        <CardContent>
          {tournaments.length > 0 ? (
            <ul className="space-y-4">
              {tournaments.map((tournament) => (
                <li key={tournament.id} className="flex flex-col sm:flex-row items-start sm:items-center justify-between gap-4 p-4 border rounded-lg bg-card hover:bg-muted/50 transition-colors">
                  <div className="flex-1">
                    <p className="font-semibold text-lg">{tournament.name}</p>
                    <p className="text-sm text-muted-foreground">{format(new Date(tournament.date), "EEEE, MMMM d, yyyy")}</p>
                     {tournament.registrationCloseDate && (
                      <p className={cn("text-xs mt-1", new Date(tournament.registrationCloseDate) < new Date() ? "text-red-600 font-medium" : "text-muted-foreground")}>
                        Registration Closes: {format(new Date(tournament.registrationCloseDate), "MMMM d, yyyy")}
                      </p>
                    )}
                  </div>
                  <div className="flex items-center gap-2 flex-shrink-0">
                    {tournament.webpageUrl && (
                      <Button variant="outline" size="sm" asChild>
                        <a href={tournament.webpageUrl} target="_blank" rel="noopener noreferrer">
                          <ExternalLink className="mr-2 h-3 w-3" /> Webpage
                        </a>
                      </Button>
                    )}
                    <Button variant="default" size="sm" asChild>
                      <Link href={`/dashboard/tournaments/${tournament.id}`}>
                        <Info className="mr-2 h-3 w-3" /> Details
                      </Link>
                    </Button>
                  </div>
                </li>
              ))}
            </ul>
          ) : (
            <div className="flex flex-col items-center justify-center rounded-lg border border-dashed p-12 text-center h-60">
              <h3 className="text-xl font-semibold font-headline">No Upcoming Tournaments</h3>
              <p className="text-muted-foreground mt-2">
                Check the Tournament Scheduler to add new tournaments.
              </p>
            </div>
          )}
        </CardContent>
      </Card>
    </div>
  );
}

